@media all  and (max-width: 1440px) {
	.carousel--show-several {
		.carousel-cell {
			padding: 60px; } }
	.carousel {
		.carousel-cell-h {
			margin-bottom: 40px; } }
	.carousel-cell-text-block {
		.carousel-cell-logo {
			margin-bottom: 35px; } } }

@media all  and (max-width: 1024px) {
	.carousel--show-several {
		.carousel-cell {
			padding: 50px; } }
	.carousel-cell-container {
		width: 90%; }
	.carousel {
		.carousel-cell-h {
			margin-bottom: 30px; } }
	.carousel-cell-text-block {
		.carousel-cell-logo {
			margin-bottom: 30px; }
		.text-block {
			margin-bottom: 20px; } } }

@media all  and (max-width: 767px) {
	.carousel-cell-text-block {
		width: 100%;
		.text-block:nth-child(2) {
			float: right; }
		.text-block {
			display: inline-block;
			width: 48%;
			vertical-align: top;
			.text-block-text {
				padding-right: 0; } } }
	.device-block {
		left: 50%;
		bottom: 30%;
		height: 200px;
		width: 60%;
		.device-block-desktop {
			max-width: 660px;
			width: 105%;
			right: 0; }
		.device-block-tablet {
			right: 75%;
			max-width: 241px;
			width: 38%; }
		.device-block-mobile {
			right: 103%;
			max-width: 115px;
			width: 18%; } } }

@media all  and (max-width: 554px) {

	.carousel {
		.carousel-cell-h {
			margin-bottom: 20px;
			font-size: 28px; } }
	.carousel--show-several {
		.carousel-cell {
			padding: 40px;
			padding-left: 45px;
			padding-right: 45px; } }
	.carousel-cell-text-block {
		width: 100%;
		.text-block:nth-child(2) {
			float: none;
			margin-bottom: 20px; }
		.text-block {
			display: inline-block;
			width: 100%;
			vertical-align: top;
			.text-block-text {
				padding-right: 0; } }
		.carousel-cms {
			margin-bottom: 10px; }
		.text-block-cms {
			margin-bottom: 0px;
			display: inline-block;
			vertical-align: middle;
			margin-right: 20px; }
		.carousel-cell-logo {
